* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  user-select: none;
}

:root {
  --bold-ff: "Futura bold";
  --medium-ff: "Futura medium";
  --light-ff: "Futura light";
  --xtrabold-ff: "Futura extrabold";

  --regular-secondary-ff: "Fira code regular";
  --medium-secondary-ff: "Fira code medium";
  --semibold-secondary-ff: "Fira code semibold";
  --bold-secondary-ff: "Fira code bold";

  --primary: white;
  --secondary: black;
  --basic-transition: 0.2s;
  --medium-transition: 0.5s;
  --long-transition: 0.8s;
  --font-size: 20px;
}

body {
  overflow: hidden;
  font-size: var(--font-size);
  font-family: var(--medium-ff);
  color: var(--primary);
  width: 100vw;
  height: 100vh;
  background: var(--secondary);
}

.container {
  max-width: 1200px;
  width: 100%;
  height: 100%;
  margin: 0 auto;
}
.section {
  width: 100vw;
  height: 100vh;
  &__inner {
    overflow: hidden;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__info {
    display: flex;
    flex-direction: column;
  }
}

.title {
  font-size: 80px;
  font-family: var(--bold-ff);
  position: relative;
  max-width: 350px;
  width: 100%;
  &::after {
    position: absolute;
    content: "";
    height: 3px;
    width: 100%;
    background: var(--primary);
    left: 0;
    bottom: -20px;
  }
}

.descr {
  margin: 40px 0 0 0;
  font-size: 25px;
}

.title,
.descr {
  opacity: 0;
}

.hovereffect {
  transition: var(--basic-transition);
  &:hover {
    opacity: 0.8;
  }
}

.show {
  animation: show 1s forwards;
}

@keyframes show {
  from {
    transform: translateX(-120%);
  }
  to {
    transform: translatex(0);
    opacity: 1;
  }
}

.rotate {
  &__upper {
    transform: rotate(45deg);
  }
  &__lower {
    transform: rotate(-45deg);
  }
}

.anim {
  transform: translateX(0) scale(1);
}
