.contact {
  &__socials {
    overflow: hidden;
    display: flex;
    align-items: center;
    margin: 15px 0 0 0;
    gap: 15px;
    opacity: 0;
    &__link {
      height: 50px;
      width: 50px;
    }
  }
  &__image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transform: scale(1.07);
    transition: 0.1s;
    &__container {
      box-shadow: 0 0 0 3px var(--primary);
      overflow: hidden;
      padding: 3px;
      width: 50vw;
      height: 50vh;
      margin: 0 5px 0 0;
      border-radius: 10px;
      transform: translatex(15%) scale(0.9);
      transition: var(--long-transition);
    }
  }
}
