.projects {
  &__app {
    display: flex;
    justify-content: center;
    flex-direction: column;
    height: 50vh;
    width: 50vw;
    background: var(--secondary);
    transition: transform var(--long-transition), width var(--medium-transition), height var(--medium-transition);
    transform: translatex(15%) scale(90%);
    margin: 0 5px 0 0;
    &__header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-top-right-radius: 10px;
      border-top-left-radius: 10px;
      width: 100%;
      height: 40px;
      background: var(--primary);
      color: var(--secondary);
      padding: 4px 12px 0;
      &__title {
        font-family: var(--semibold-secondary-ff);
      }
      &__buttons {
        width: 90px;
        height: 100%;
        display: flex;
        justify-content: left;
        align-items: center;
      }
      &__button {
        position: relative;
        width: 30px;
        height: 30px;
      }
    }
    &__textarea {
      width: 100%;
      height: 100%;
      padding: 15px 20px;
      font-size: var(--font-size);
      font-family: var(--medium-secondary-ff);
      color: var(--primary);
      background: var(--secondary);
      border: none;
      outline: none;
      resize: none;
      white-space: normal;
      overflow: hidden;
      box-shadow: inset 0 0 0 3px var(--primary);
      border-bottom-right-radius: 10px;
      border-bottom-left-radius: 10px;
    }
  }
  &__link {
    width: 100%;
    height: 100%;
    padding: 10px 40px;
    text-align: center;
    font-size: var(--font-size);
    background: var(--primary);
    color: var(--secondary);
    text-decoration: none;
    border-radius: 5px;
    &:hover {
      opacity: 0.8;
    }
  }
  &__button {
    width: 200px;
    margin: 30px 0 0 0;
    opacity: 0;
  }
}

.minimize {
  &::before {
    position: absolute;
    content: "";
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 15px;
    height: 2px;
    background: var(--secondary);
  }
}

.expand {
  &::before {
    position: absolute;
    content: "";
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 12px;
    height: 12px;
    border: 2px inset var(--secondary);
    border-radius: 50%;
  }
}

.close {
  &::before,
  &::after {
    position: absolute;
    content: "";
    width: 15px;
    height: 2px;
    background: var(--secondary);
  }
  &::before {
    top: 20px;
    left: 13px;
    transform: rotate(45deg) translate(-50%, -50%);
  }
  &::after {
    bottom: 19px;
    left: 14px;
    transform: rotate(-45deg) translate(-50%, -50%);
  }
}

.closed {
  animation: close var(--long-transition) forwards;
}

@keyframes close {
  0% {
    transform: translatex(0);
  }
  100% {
    transform: translatex(120%);
  }
}

.maximized {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 100%;
}
